.modal_backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: var(--backdrop);
  z-index: 111111;
}

.modal_content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-height: 433px;
  /* width: 350px; */

  border-radius: 10px;
  padding: 15px;
  background-color: var(--modal-back);
}
