.modal_backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;

  background-color: var(--backdrop);
  z-index: 9999;

  /*   background-color: var(--backdrop);
  z-index: 3; */
}

.modal_content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-height: 300px;
  /* max-width: 800px; */
  /* width: 100%; */
  border-radius: 10px;
  padding: 15px;
  background-color: var(--modal-back);
}
